.item {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;

    box-shadow: 5px 5px 10px 0px rgba(34, 60, 80, 0.2);
}

.item:not(:last-child) {
    margin-bottom: 15px;
}

.status {
    width: 10px;
    height: 10px;
    margin-right: 10px; 

    border-radius: 50%;
}

.online { 
    composes: status;
    background-color: var(--green-color);
}


.offline { 
    composes: status;
    background-color: var(--red-color)}

.avatar { 
    margin-right: 10px;
}
